<template>
  <group class="jq-mt">
     <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
  </group>
 <group class="jq-mt">
   <div class="chart" v-epie="pieData"></div>
 </group>

</template>
  <script>
    import {Group,cell,PopupPicker} from 'vux/src/components'
    import { getList } from '../../../untils/api'
    export default {
    components: {
    Group,
    PopupPicker,
    cell,
    },
    data () {
    return {
    dateTile:'日期',
    time: formatData.defaultTime,
    timeList: formatData.dateList,
    permissions:localStorage.getItem("permissions"),
    pieData:{}
    }
    },
    ready(){
      document.title='制造成本细项';
    },
    methods: {
    async getData() {
        commit('UPDATE_LOADING', true)
       let id ='';
     if(this.permissions=='201'){
          id = "APP10302a";
      }else if(this.permissions=='203'){
         id = "APP10302b";
      }

    var date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
   
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.pieData = {};
    this.pieData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
    watch: {
     time (newtime, oldtime) {
      this.getData();
       formatData.setDateTime(newtime);
    }
  },
    created: function () {
    this.pieData = {};
    this.pieData.dataset = [];
    this.getData();
    }
    }
  </script>






